import { clips } from './render';

const swipedetect = (el, callback) => {
  const touchsurface = el;
  let swipedir;
  let startX;
  let startY;
  let distX;
  let distY;
  const threshold = 150;
  const restraint = 100;
  const allowedTime = 300;
  let elapsedTime;
  let startTime;
  const handleswipe = callback || function (swipedir) {};
  clips.addEventListener('mousedown', (e) => {
    swipedir = 'none';
    startX = e.pageX;
    startY = e.pageY;
    startTime = new Date().getTime();
    e.preventDefault();
  }, false);
  clips.addEventListener('mouseup', (e) => {
    distX = e.pageX - startX;
    distY = e.pageY - startY;
    elapsedTime = new Date().getTime() - startTime;
    if (elapsedTime <= allowedTime) {
      if (Math.abs(distX) >= threshold && Math.abs(distY) <= restraint) {
        swipedir = (distX < 0) ? 'left' : 'right';
      }
    }
    handleswipe(swipedir);
    e.preventDefault();
  }, false);
  touchsurface.addEventListener('touchstart', (e) => {
    const touchobj = e.changedTouches[0];
    swipedir = 'none';
    startX = touchobj.pageX;
    startY = touchobj.pageY;
    startTime = new Date().getTime();
    e.preventDefault();
  }, false);
  touchsurface.addEventListener('touchmove', (e) => {
    e.preventDefault();
  }, false);
  touchsurface.addEventListener('touchend', (e) => {
    const touchobj = e.changedTouches[0];
    distX = touchobj.pageX - startX;
    distY = touchobj.pageY - startY;
    elapsedTime = new Date().getTime() - startTime;
    if (elapsedTime <= allowedTime) {
      if (Math.abs(distX) >= threshold && Math.abs(distY) <= restraint) {
        swipedir = (distX < 0) ? 'left' : 'right';
      }
    }
    handleswipe(swipedir);
    e.preventDefault();
  }, false);
};

export { swipedetect as default };
