import { CLIPS_NUMBER, DATA, searchRequest, loadData } from './request';
import swipedetect from './swipe';

let clipsPerPage;
let indexCurrentPage = 0;

const wrapper = document.createElement('div');
wrapper.className = 'wrapper';

wrapper.innerHTML = '\
<div class="container">\
  <input type="text" class="input" placeholder="Enter a query">\
  <input type="button" value="search" class="search-btn">\
</div>\
';

document.body.appendChild(wrapper);

export const searchButton = document.querySelector('.search-btn');

const pages = document.createElement('div');
pages.className = 'pages';

export const clips = document.createElement('div');
clips.className = 'clips';
document.body.appendChild(clips);
document.body.appendChild(pages);

export const renderPage = () => {
  clips.innerHTML = '';
  const width = document.body.clientWidth;
  if (width > 1024) {
    clipsPerPage = 5;
  } else if (width < 1024 && width > 500) {
    clipsPerPage = 3;
  } else if (width < 500) {
    clipsPerPage = 2;
  }
  DATA.items.forEach((item, index) => {
    if (index > indexCurrentPage * clipsPerPage
      && index < indexCurrentPage * clipsPerPage + clipsPerPage) {
      clips.appendChild(renderClip(item));
    }
  });
  if (clips.childNodes.length === 0) {
    clips.innerHTML = '<span>Loading...</span>';
  }
  document.body.appendChild(renderPaging(DATA));
};

const renderClip = (item) => {
  const clip = document.createElement('div');
  clip.className = 'clip';
  clip.innerHTML = `
        <span>${item.snippet.title}</span>
        <a href='https://www.youtube.com/watch?v=${item.id.videoId}' target='_blank'><img src=${item.snippet.thumbnails.medium.url}></a>
        <div class="video-info">
          <div class="channel-name"><i class="fas fa-male"></i><span>${item.snippet.channelTitle}</span></div>
          <div class="date"><i class="far fa-calendar-alt"></i><span>${item.snippet.publishedAt.slice(0, 10)}</span></div>
          <div class="views"><i class="far fa-eye"></i><span>${item.statistics.viewCount}</span></div>
          <p>${item.snippet.description}</p>
        </div>`;
  return clip;
};

const renderDot = (count) => {
  const dot = document.createElement('div');
  dot.className = 'dot';
  dot.innerHTML = count;
  if (count === indexCurrentPage) {
    dot.classList.add('selected');
  }
  dot.onclick = () => onClickHandler(count);
  return dot;
};

const renderPaging = (data) => {
  pages.innerHTML = '';
  if (data.pageInfo.totalResults > CLIPS_NUMBER) {
    const startIndex = indexCurrentPage < 2 ? 0 : indexCurrentPage - 2;
    const endIndex = indexCurrentPage < 2 ? 5 : indexCurrentPage + 3;
    for (let i = startIndex; i < endIndex; i += 1) {
      pages.appendChild(renderDot(i));
    }
  } else {
    pages.innerHTML = '';
  }
  return pages;
};

const onClickHandler = (count) => {
  if (count !== indexCurrentPage) {
    indexCurrentPage = count;
    renderPaging(DATA);
  }
  indexCurrentPage = count;
  renderPage();
  if (DATA.items.length - indexCurrentPage * 5 < 10) {
    loadData();
  }
};

window.addEventListener('keydown', (e) => {
  if (e.keyCode === 13) {
    searchRequest();
  }
  if (e.keyCode === 37 && indexCurrentPage > 0) {
    indexCurrentPage -= 1;
    renderPaging(DATA);
    renderPage();
  }
  if (e.keyCode === 39) {
    indexCurrentPage += 1;
    renderPaging(DATA);
    renderPage();
  }
});

clips.addEventListener('mousedown', () => {
  const dot = document.querySelector('.selected');
  dot.classList.add('tooltip');
  dot.setAttribute('data-content', `Current Page ${indexCurrentPage}`);
});
clips.addEventListener('mouseup', () => {
  const dot = document.querySelector('.selected');
  dot.removeAttribute('data-content');
  dot.classList.remove('tooltip');
});

swipedetect(clips, (swipedir) => {
  if (swipedir === 'left') {
    indexCurrentPage += 1;
    renderPaging(DATA);
    renderPage();
  } else
  if (swipedir === 'right' && indexCurrentPage > 0) {
    indexCurrentPage -= 1;
    renderPaging(DATA);
    renderPage();
  }
});

searchButton.onclick = () => searchRequest();
